.levelCalc {
    display: grid;
    grid-template-columns: 150px 60px 1fr;
}

.levelCalc label {
    text-align: right;
    padding-right: 0.5em;
    align-self: center;
}

.levelCalc p {
    grid-column: 3;
    grid-row: 1 / 3;
    margin: 0;
    margin-left: 0.5em;
    align-self: center;
}
