* {
    box-sizing: border-box;
}

html {
    font-size: 16px;
    scroll-behavior: smooth;
}

:root {
    --w-max: 1120px;

    --clr-l-blue: #7bc2e2;
    --clr-l-blue-o: 123, 194, 226;
    --clr-ll-blue-table: #b0def0;
    --clr-ll-blue: #9ad3ea;
    --clr-ll-blue-o: 154, 211, 234;
    --clr-yellow: #ffff4a;

    --link-clr-1: #631bcf;
    --link-clr-2: #a471f1;
}

::-webkit-scrollbar-track {
    background-color: transparent;
}

::-webkit-scrollbar {
    width: 7px;
}

::-webkit-scrollbar-thumb {
    background-color: #ffff4a;
    background-color: var(--clr-yellow);
    border: 0;
    border-radius: 50px;
}

body {
    font-family: 'Poppins', sans-serif;
    font-size: 0.975rem;
    font-weight: 400;
    overflow: overlay;
}

body #root {
    display: flex;
    flex-direction: column;
}

a,
a:visited,
a:active {
    color: #631bcf;
    color: var(--link-clr-1);
    text-decoration: none;
}

a:hover,
a:focus {
    color: #a471f1;
    color: var(--link-clr-2);
    text-decoration: underline;
    outline: none;
}

.anchor-Zone {
    scroll-margin-top: 65px;
}

section {
    margin-top: 1rem;
}

section > div {
    padding: 0.2em 0.7em;
}

.content {
    width: 100%;
    min-height: 85vh;
    max-width: 1120px;
    max-width: var(--w-max);
    margin: 5rem auto;
    padding: 1em 1.5em;
    background-color: rgba(154, 211, 234, 0.9);
    background-color: rgba(var(--clr-ll-blue-o), 0.9);
    border-radius: 1.2em;
}

.sprite {
    width: 32px;
    height: 32px;
}

h4 {
    margin: 0.5em 0;
}

.bold {
    font-weight: bold;
}

.underline {
    text-transform: underline;
}

.no-list-style {
    list-style: none;
    padding-left: 0.5em;
}

.button-1 {
    background: none;
    border: none;
    font-size: inherit;
    font-family: inherit;
    outline: none;

    padding: 0.4em;
    background: var(--clr-l-blue);
    border: 1px solid black;
    font-weight: bold;
    margin: 0.2em 0.5em;
    cursor: pointer;
}

.grid-1 {
    display: grid;
    grid-template-columns: repeat(auto-fill, 350px);
}

.grid-1 > table {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.navigation {
    width: 100%;
    position: fixed;
    padding-bottom: 0.5em;
    background-color: var(--clr-l-blue);
    border-bottom: 2px solid rgb(63, 127, 148);
    z-index: 1;
}

.navigation ul {
    width: 100%;
    max-width: var(--w-max);
    margin: 0 auto;
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: flex-end;
}

.navigation li {
    font-size: 1.2rem;
    padding: 0.7em 0.7em 0.5em;
    text-shadow: 1px 1px 1px black;
}

.navigation a,
.navigation a:visited,
.navigation a:active {
    display: inline-block;
    text-decoration: none;
    color: var(--clr-yellow);

    transition: transform 200ms ease-in;
}

.navigation a::after,
.navigation a:visited::after,
.navigation a:active::after {
    position: absolute;
    bottom: -0.2em;
    left: 50%;
    content: '';
    width: 0%;
    height: 2px;
    background-color: var(--clr-yellow);
    border-radius: 0.2em;
    box-shadow: 1px 1px 1px black;

    transition: all 150ms ease-in;
}

.navigation a:hover,
.navigation a:focus,
.navigation .active {
    outline: none;
    transform: scale(1.05);
    position: relative;
}

.navigation a:hover::after,
.navigation a:focus::after,
.navigation .active::after {
    width: 100%;
    left: 0%;
}

.footer {
    width: 100%;
    background-color: rgba(var(--clr-l-blue-o), 0.6);
    text-align: center;
    font-size: 0.9rem;
    font-style: italic;
    z-index: 1;
}

.header-2 {
    font-size: 2rem;
    letter-spacing: 0.7px;
    margin: 0.3em 0 0.5em 0;
    position: relative;
}

.header-2::after {
    content: '';
    position: absolute;
    width: 100%;
    top: 100%;
    left: 0;
    border-bottom: 1px solid black;
}

.header-3 {
    font-size: 1.5rem;
    letter-spacing: 0.7px;
    margin: 0.3em 0 0.3em 0;
    position: relative;
}

.header-3::after {
    content: '';
    position: absolute;
    width: 100%;
    top: 115%;
    left: 0;
    border-bottom: 1px solid black;
}

.expandCollapse {
    margin: 0;
    padding: 0;
    padding-left: 0.4em;
    cursor: pointer;
    background: none;
    outline: none;
    font-size: inherit;
    color: inherit;
    border: 0;
    font-size: 0.55em;
}

.pageHeader {
    display: grid;
    grid-template-columns: 1fr -webkit-max-content;
    grid-template-columns: 1fr max-content;
    grid-gap: 80px;
    padding: 0 0.7em;
}

.quickLinks {
    background-color: var(--clr-ll-blue-table);
    padding: 0.3em 1.5em;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    font-size: 1rem;
    border: 1px solid black;
    margin-right: 1.5em;
    display: grid;
    grid-template-columns: repeat(2, -webkit-max-content);
    grid-template-columns: repeat(2, max-content);
    grid-column-gap: 1.2em;
    -webkit-column-gap: 1.2em;
            column-gap: 1.2em;
}

.quickLinks p {
    grid-column: 1 / -1;
    text-align: center;
    margin: 0.5em 0;
    text-decoration: underline;
}

.quickLinks ol {
    list-style: none;
    padding: 0;
}

.quickLinks li {
    margin: 0.3em 0;
}

.number {
    display: inline-block;
    width: 1em;
    text-align: right;
    margin-right: 0.35em;
}

.table {
    border-collapse: collapse;
    text-align: center;
    margin: 2rem 0;
}

.table tr {
    position: relative;
}

.table th {
    font-weight: bold;
    font-size: 105%;
    background-color: var(--clr-l-blue);
    padding: 0.4em 0.5em;
    border: 2px solid rgb(63, 127, 148);
    background-clip: padding-box;
}

.table td {
    background-color: rgba(var(--clr-l-blue-o), 0.45);
    background-color: var(--clr-ll-blue-table);
    padding: 0.4em 0.65em;
    border: 2px solid rgb(63, 127, 148);
    background-clip: padding-box;
}

.table p {
    margin: 0;
    padding: 0;
}

.table .maxScroll {
    max-height: 80px;
    overflow: overlay;
}

.table .maxScroll ul {
    list-style: none;
    padding: 0.2em 0;
    padding-right: 0.4em;
    margin: 0;
    width: 100%;
}

.table .maxScroll::-webkit-scrollbar {
    width: 5px;
}

.table .maxScroll::-webkit-scrollbar-thumb {
    background-color: var(--clr-l-blue);
}

.table .maxScroll li {
    cursor: default;
    margin: 0;
    padding: 0;
}

@-moz-document url-prefix() {
    .table .maxScroll {
        max-height: unset;
        overflow: hidden;
    }
}

.levelCalc {
    display: grid;
    grid-template-columns: 150px 60px 1fr;
}

.levelCalc label {
    text-align: right;
    padding-right: 0.5em;
    align-self: center;
}

.levelCalc p {
    grid-column: 3;
    grid-row: 1 / 3;
    margin: 0;
    margin-left: 0.5em;
    align-self: center;
}

